@charset "UTF-8";
@font-face {
  font-family: Montserrat;
  font-display: swap;
  src: url("../fonts/Montserrat-Regular.woff2") format("woff2");
  font-weight: 400;
  font-style: normal;
}
/* ------------------------------------------------------------------------------------------------------------------------------
ROOT
--------------------------------------------------------------------------------------------------------------------------------*/
:root {
  --container-width: 1250px;
  --container-padding-desktop: 30px;
  --container-padding-large: 15px;
  --container-padding-small: 0px;
  --font-main: "Montserrat", sans-serif;
  --font-title: "Montserrat", sans-serif;
  --white: white;
  --white-85: rgb(255, 255, 255, 0.85);
  --black: black;
  --accent: #0096D3;
  --gray: #F8F6F8;
  --text: rgb(47, 47, 47);
  --scrollbar: gray;
  --scrollbar-track: gray;
  --scrollbar-thumb: var(--accent);
}

/* ------------------------------------------------------------------------------------------------------------------------------
TYPOGRAPHY
--------------------------------------------------------------------------------------------------------------------------------*/
h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: var(--font-title);
}

/* ------------------------------------------------------------------------------------------------------------------------------
BASE
--------------------------------------------------------------------------------------------------------------------------------*/
* {
  margin: 0;
  border: 0;
  padding: 0;
  -webkit-tap-highlight-color: transparent;
}
*, *::before, *::after {
  box-sizing: border-box;
}
*:focus, *:active {
  outline: none;
}

nav,
footer,
header,
aside {
  display: block;
}

html {
  scroll-behavior: smooth;
  opacity: 0.5;
  transition: opacity 0.65s ease-in-out;
}
html.loaded {
  opacity: 1;
}

html,
body {
  inline-size: 100%;
  block-size: 100%;
}

body {
  overscroll-behavior: none;
  text-rendering: optimizelegibility;
  text-size-adjust: 100%;
  font-family: var(--font-main);
  font-size: 1rem;
  font-weight: 400;
  -moz-osx-font-smoothing: grayscale;
  -webkit-font-smoothing: antialiased;
  text-wrap: balance;
}

svg {
  max-width: 50px;
  max-height: 50px;
}

.container {
  margin-inline: auto;
  min-inline-size: 20rem;
  max-inline-size: var(--container-width);
  padding-inline: var(--container-padding-desktop);
}
@media (max-width: 992px) {
  .container {
    padding-inline: var(--container-padding-large);
  }
}
@media (max-width: 576px) {
  .container {
    padding-inline: var(--container-padding-small);
  }
}

.img-responsive {
  display: block;
  width: 100%;
  height: auto;
  object-fit: cover;
  overflow: hidden;
}

.image-bg {
  position: relative;
}
.image-bg img {
  display: block;
  position: absolute;
  inset-block-start: 0;
  inset-inline-start: 0;
  object-fit: cover;
  block-size: 100%;
  inline-size: 100%;
}

.wrapper {
  display: flex;
  min-block-size: 100%;
  /** Поменять на clip - если используется в проекте position: sticky */
  overflow: clip;
  flex-direction: column;
}

.main {
  position: relative;
  flex: 1 1 auto;
}

/* ------------------------------------------------------------------------------------------------------------------------------
COMPONENTS
--------------------------------------------------------------------------------------------------------------------------------*/
.main {
  background-color: var(--gray);
  padding-block-start: 15px;
}
@media (max-width: 576px) {
  .main {
    padding-block-start: 7px;
  }
}

.telegram {
  height: 40px;
  border-radius: 12px;
  border: var(--accent) 2px solid;
  background-color: var(--white);
  padding-inline: 10px;
  padding-block: 10px;
  display: flex;
  align-items: center;
  gap: 10px;
  font-size: 12px;
  color: var(--text);
  font-weight: bold;
  text-decoration: none;
  transition: all 0.3s ease-in-out;
}
@media (max-width: 576px) {
  .telegram {
    gap: 12px;
    font-size: 9px;
    margin-inline: 15px;
    line-height: 125%;
  }
}
.telegram svg {
  flex-shrink: 0;
  width: 20px;
  aspect-ratio: 1/1;
  fill: var(--accent);
}
.telegram:hover {
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.12);
}

.custom {
  position: relative;
  display: flex;
  gap: 30px;
  flex-direction: column;
}
@media (max-width: 992px) {
  .custom {
    gap: 15px;
  }
}
@media (max-width: 576px) {
  .custom {
    gap: 12px;
  }
}

.custom-group {
  display: flex;
  align-items: center;
  gap: 12px;
  width: 100%;
  user-select: none;
}
.custom-group > * {
  user-select: none;
}
@media (max-width: 576px) {
  .custom-group {
    transform: translateX(-31.85%);
    width: 275%;
    gap: 7px;
  }
}
.custom-group__banner {
  display: block;
  border-radius: 12px;
  overflow: hidden;
}
.custom-group__banner:nth-child(1) {
  width: 31.5%;
}
.custom-group__banner:nth-child(2) {
  width: 37%;
}
.custom-group__banner:nth-child(3) {
  width: 31.5%;
}
@media (max-width: 576px) {
  .custom-group__banner:nth-child(1) {
    width: 33.33%;
  }
  .custom-group__banner:nth-child(2) {
    width: 33.33%;
  }
  .custom-group__banner:nth-child(3) {
    width: 33.33%;
  }
}
.custom-group__banner img {
  display: block;
  width: 100%;
  height: auto;
  object-fit: cover;
  overflow: hidden;
  user-select: none;
}
.custom-group a.custom-group__banner {
  transition: all 0.3s ease-in-out;
}
.custom-group a.custom-group__banner:hover {
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.12);
}

.tile {
  border-radius: 20px 20px 0 0;
  background-color: var(--white);
  padding: 10px 45px 45px 45px;
}
.tile h2 {
  font-size: 24px;
  font-weight: bold;
  color: var(--text);
  padding-block-start: 10px;
  padding-block-end: 20px;
}
@media (max-width: 576px) {
  .tile h2 {
    padding-block-start: 10px;
    padding-block-end: 10px;
  }
}
@media (max-width: 992px) {
  .tile {
    padding: 10px 25px 45px 25px;
  }
  .tile h2 {
    font-size: 22px;
  }
}
@media (max-width: 576px) {
  .tile {
    padding-inline: 15px;
  }
  .tile h2 {
    font-size: 15px;
  }
}

.transform-scalewrap {
  transition: all 0.3s ease-in-out;
}
.transform-scalewrap:hover {
  transform: scale(1.05);
}

@media (max-width: 576px) {
  .wiggle-slow {
    animation: wiggle 1.5s ease-in-out infinite alternate;
  }
}

@keyframes wiggle {
  50% {
    transform: translateX(-31.35%);
  }
  100% {
    transform: translateX(-32.35%);
  }
}
:root {
  --dynamic-site-width: 0px; /* будем обновлять через JS */
}

/* Стили для кнопки */
#back-to-top {
  position: fixed;
  bottom: 90px;
  right: calc(50% + var(--dynamic-site-width) / 2 - 5px);
  transform: translateX(100%);
  background-color: var(--accent);
  width: 50px;
  height: 50px;
  border-radius: 50%;
  cursor: pointer;
  visibility: hidden;
  opacity: 0;
  transition: all 0.5s ease-in-out;
  z-index: 9999;
}
@media (max-width: 576px) {
  #back-to-top {
    display: none;
  }
}
#back-to-top svg {
  width: 100%;
  aspect-ratio: 1/1;
  fill: white;
}

#back-to-top.is-active {
  visibility: visible;
  opacity: 1;
}

#back-to-top:hover {
  filter: brightness(1.2);
}

/* ------------------------------------------------------------------------------------------------------------------------------
FOOTER
--------------------------------------------------------------------------------------------------------------------------------*/
.footer {
  background-color: var(--accent);
  height: 70px;
  padding-block-start: 20px;
  padding-block-end: 10px;
  font-size: 13px;
  color: var(--white-85);
}
@media (max-width: 576px) {
  .footer {
    height: 55px;
    font-size: 11px;
  }
}
@media (max-width: 440px) {
  .footer {
    font-size: 8.5px;
  }
}
.footer .justify-content-between {
  align-items: end;
  padding-inline: 45px;
}
@media (max-width: 992px) {
  .footer .justify-content-between {
    padding-inline: 30px;
  }
}
@media (max-width: 576px) {
  .footer .justify-content-between {
    padding-inline: 15px;
  }
}
.footer ul {
  margin: 0;
  padding: 0;
  list-style: none;
  text-align: right;
}
.footer ul li:first-child {
  margin-bottom: 2px;
}
@media (max-width: 576px) {
  .footer ul li:first-child {
    margin-bottom: 4px;
  }
}
.footer a {
  color: var(--white-85);
}
.footer a:hover {
  text-decoration: none;
}

/* ------------------------------------------------------------------------------------------------------------------------------
HEADER
--------------------------------------------------------------------------------------------------------------------------------*/
.header {
  position: sticky;
  top: 0;
  z-index: 12000;
  background-color: var(--accent);
}
.header .logo {
  width: 100px;
}
.header .logo svg {
  min-width: 100%;
  width: 100%;
  aspect-ratio: 332/88;
  fill: white;
}
.header .justify-content-end {
  display: flex;
  align-items: center;
  min-height: 50px;
}
@media (max-width: 576px) {
  .header .justify-content-end {
    padding-right: 15px;
    padding-left: 15px;
  }
}

/* ------------------------------------------------------------------------------------------------------------------------------
POPUP Swiper
------------------------------------------------------------------------------------------------------------------ */
.popup {
  position: absolute;
  top: 50px;
  left: 0;
  width: 100%;
  height: 100vh;
  opacity: 0;
  visibility: hidden;
  z-index: 9000;
  transition: all 1s ease-in-out;
}
@media (max-width: 1200px) {
  .popup {
    top: -15px;
  }
}

.popup.show {
  opacity: 1;
  visibility: visible;
}

.popup__window {
  position: absolute;
  z-index: 9999;
  background: #fff;
  display: flex;
  flex-direction: column;
  justify-content: center;
  width: 100%;
  max-width: 1440px;
  min-height: calc(100vh - 70px);
  padding: 80px 45px 30px 45px;
  transition: opacity 0.25s ease, transform 0.25s ease;
  border-bottom: 1px solid rgba(0, 150, 211, 0.25);
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.2);
  user-select: none;
}
@media (max-width: 1200px) {
  .popup__window {
    padding: 60px 45px 25px 45px;
  }
}
@media (max-width: 992px) {
  .popup__window {
    padding: 30px;
  }
}
@media (max-width: 576px) {
  .popup__window {
    padding: 15px;
  }
}

.popup__close {
  position: absolute;
  right: 20px;
  top: 20px;
  background-color: #f6f6f5;
  border-radius: 5px;
  width: 25px;
  height: 25px;
  cursor: pointer;
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 100;
  border: 1px solid rgba(0, 150, 211, 0.05);
}

.popup__close:hover svg {
  fill: var(--accent);
}

.popup__close svg {
  fill: #bbbbba;
  width: 100%;
  height: 100%;
  transition: fill 0.3s ease-in-out;
}

.popup__title {
  font-size: 24px;
  font-weight: bold;
  color: var(--text);
  padding-top: 60px;
}
@media (max-width: 1200px) {
  .popup__title {
    padding-top: 40px;
  }
}
@media (max-width: 992px) {
  .popup__title {
    font-size: 22px;
  }
}
@media (max-width: 576px) {
  .popup__title {
    font-size: 15px;
    padding-top: 5px;
  }
}

/* ---------------------- Swiper ---------------------- */
:root {
  --swiper-navigation-sides-offset: -25px;
}
@media (max-width: 992px) {
  :root {
    --swiper-navigation-sides-offset: -20px;
  }
}

.sliderModal,
.slider {
  position: relative;
}
.sliderModal .product-slide,
.sliderModal .product-variant,
.slider .product-slide,
.slider .product-variant {
  display: block;
  border-radius: 12px;
  border: 1px solid transparent;
  cursor: pointer;
  overflow: hidden;
  transition: all 0.3s ease-in-out;
}
.sliderModal .product-slide:hover,
.sliderModal .product-variant:hover,
.slider .product-slide:hover,
.slider .product-variant:hover {
  border: 1px solid rgba(0, 150, 211, 0.25);
}
.sliderModal .product-slide img,
.sliderModal .product-variant img,
.slider .product-slide img,
.slider .product-variant img {
  display: block;
  width: 100%;
  height: auto;
  object-fit: cover;
  overflow: hidden;
}
.sliderModal.hovered .swiper-button-prev,
.slider.hovered .swiper-button-prev {
  transform: translateX(0);
}
.sliderModal.hovered .swiper-button-next,
.slider.hovered .swiper-button-next {
  transform: translateX(0);
}

.swiper.product-variant {
  display: block;
  border-radius: 12px;
  overflow: hidden;
  border: 1px solid transparent;
  transition: all 0.3s ease-in-out;
}

.swiper.product-variant:hover {
  border: 1px solid rgba(0, 150, 211, 0.25);
}

.swiper.product-variant img {
  display: block;
  width: 100%;
  height: auto;
  object-fit: cover;
}

/* Стрелки Swiper */
.swiper-button-prev,
.swiper-button-next {
  width: 50px;
  height: 50px;
  background-color: var(--white);
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute;
  top: 50%;
  cursor: pointer;
  z-index: 10;
  transition: all 0.65s ease-in-out;
}
@media (max-width: 992px) {
  .swiper-button-prev,
  .swiper-button-next {
    width: 40px;
    height: 40px;
  }
}
@media (max-width: 576px) {
  .swiper-button-prev,
  .swiper-button-next {
    display: none;
  }
}

.swiper-button-prev {
  left: 0;
  transform: translateX(-30vw);
}

.swiper-button-next {
  right: 0;
  transform: translateX(30vw);
}

.swiper-button-prev svg,
.swiper-button-next svg {
  width: 100%;
  aspect-ratio: 1/1;
  fill: #bbbbba;
  transition: fill 0.3s ease-in-out;
}

.swiper-button-prev:hover svg,
.swiper-button-next:hover svg {
  fill: var(--accent);
}

/* Пагинация */
.swiper-pagination {
  position: relative !important;
  width: 100%;
  text-align: center;
  padding-top: 5px;
  display: none;
}
@media (max-width: 576px) {
  .swiper-pagination {
    display: block;
  }
}

.swiper-pagination-bullet {
  width: 5px;
  height: 5px;
  background: #bbbbba;
  opacity: 0.5;
  margin: 0 4px;
  display: inline-block;
  border-radius: 50%;
  cursor: pointer;
  transition: all 0.3s ease-in-out;
}

.swiper-pagination-bullet-active {
  background: var(--accent);
  opacity: 1;
}

.sliderModal .swiper-button-prev {
  transform: translateX(0);
}
.sliderModal .swiper-button-next {
  transform: translateX(0);
}

/* ------------------------------------------------------------------------------------------------------------------------------
KEYFRAMES
--------------------------------------------------------------------------------------------------------------------------------*/
@keyframes show-header {
  0% {
    inset-block-start: -100%;
  }
  100% {
    inset-block-start: 0;
  }
}
@keyframes fade-up-big {
  0% {
    opacity: 0;
    transform: translateY(1rem);
  }
  100% {
    opacity: 1;
    transform: translateY(0);
  }
}
@keyframes fade-up {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
@keyframes rotation {
  0% {
    transform: rotate(0);
  }
  100% {
    transform: rotate(359deg);
  }
}
/* ------------------------------------------------------------------------------------------------------------------------------
MEDIAQUERIES 
--------------------------------------------------------------------------------------------------------------------------------*/